import { MainLayout, MainLayoutClasses } from 'components/layouts/MainLayout';
import { ThankYouTemplate } from 'components/pageTemplates/ThankYouTemplate';
import { withServerSideProps } from 'helpers/withServerSideProps';
import { prefetchCurrencies } from 'hooks/queries/useGetCurrencies';
import { prefetchLocales } from 'hooks/queries/useGetLocales';
import { prefetchReservation, useGetReservation } from 'hooks/queries/useGetReservation';
import { RESERVATION_RESPONSE_SOURCES } from 'hooks/queries/useMutationReservation';
import { get } from 'lodash';
import { NextPage } from 'next';
import { useRouter } from 'next/router';
import { NextSeoProvider } from 'providers/NextSeoProvider';
import { styled } from 'theme/index';

const PREFIX = 'ThankYouPage';

const StyledMainLayout = styled(MainLayout, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(() => ({
  [`&& .${MainLayoutClasses.main}`]: {
    alignItems: 'center',
    display: 'flex',
  },
}));

const ThankYouPage: NextPage = () => {
  const { query } = useRouter();

  const reference = get(query, RESERVATION_RESPONSE_SOURCES['reference']) as string;
  const { data: reservation, isSuccess } = useGetReservation(reference);

  return (
    <>
      <NextSeoProvider title={reservation?.reference} />
      <StyledMainLayout>
        {isSuccess && reservation && <ThankYouTemplate reservationData={reservation} />}
      </StyledMainLayout>
    </>
  );
};

export default ThankYouPage;

export const getServerSideProps = withServerSideProps({
  fetchers: [prefetchReservation, prefetchLocales, prefetchCurrencies],
});
