import { fetchClient } from '@packages/providers';
import { Identifier } from '@packages/types';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { thankYouRoute } from 'application/constants';
import { RESERVATIONS_RESOURCE } from 'application/resources';
import { GetServerSidePropsContext } from 'next';

import { ReservationResponseData, reservationResponseZodSchema } from '../useMutationReservation';

const getQueryKey = (reference: string) => [reference, 'reservation', 'getOne'];

type ListingQueryKey = ReturnType<typeof getQueryKey>;

const getReservation: QueryFunction<ReservationResponseData, ListingQueryKey> = async (params) => {
  const { queryKey, meta } = params;

  const [reference] = queryKey;

  const context = meta?.context as GetServerSidePropsContext;

  const res = await fetchClient.get(
    { pathname: `${RESERVATIONS_RESOURCE}/${reference}` },
    { context },
  );

  return reservationResponseZodSchema.parse(res.data.data);
};

export const prefetchReservation = (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  const reference = context.query?.[thankYouRoute.param];

  return queryClient.prefetchQuery({
    queryKey: getQueryKey(typeof reference === 'string' ? reference : ''),
    queryFn: getReservation,
    meta: { context },
  });
};

export type UseGetReservationParams = {
  reference?: boolean;
};

export const useGetReservation = (reference?: Identifier) => {
  return useQuery({
    queryKey: getQueryKey(typeof reference === 'string' ? reference : ''),
    queryFn: getReservation,
    refetchOnMount: false,
    enabled: !!reference,
  });
};
