import { FC } from 'react';

import { Box, Container, Divider, generateUtilityClasses, Grid, Typography } from '@mui/material';
import { NextImage } from '@packages/components';
import { getFormattedDateAndTime } from '@packages/helpers';
import {
  ACCOMMODATES_SOURCE,
  BATHROOMS_SOURCE,
  BEDROOMS_SOURCE,
  HOUSE_RULES_SOURCE,
  listingRoute,
  PURPOSE_TYPES,
  RATING_COUNT_SLUG,
  RATING_SLUG,
} from 'application/constants';
import NextLink from 'components/links/NextLink';
import { getFilterValues } from 'helpers/getFilterValues';
import { getValueFromFilter } from 'helpers/getValueFromFilter';
import { useGetFilterAttributes } from 'hooks/index';
import { useGetFilterLabels } from 'hooks/queries/useGetFilterLabels';
import { ReservationResponseData } from 'hooks/queries/useMutationReservation';
import { useTranslate } from 'hooks/useTranslate';
import { get } from 'lodash';
import pluralize from 'pluralize';
import { styled } from 'theme/index';
import AmenitiesStackView from 'views/AmenitiesStackView';
import { NoImageView } from 'views/NoImageView';
import RatingView from 'views/RatingView';
import RequirementView from 'views/RequirementView/RequirementView';

export type ThankYouTemplateProps = {
  reservationData: ReservationResponseData;
};

const PREFIX = 'ThankYouTemplate';

export const ThankYouTemplateClasses = generateUtilityClasses(PREFIX, ['link']);

const StyledThankYouTemplateContainer = styled(Container, {
  name: `Styled--${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`& .${ThankYouTemplateClasses.link}`]: {
      color: theme.palette.text.primary,
      ...theme.typography.buttonUnderline,
    },
  };
});

export const ThankYouTemplate: FC<ThankYouTemplateProps> = (props) => {
  const {
    reservationData: {
      reference,
      checkin_at,
      checkout_at,
      users_count,
      listing: { address, country, slug, gallery, title, formatted_filters },
    },
  } = props;

  const countryTitle = get(country, 'title');
  const translate = useTranslate({ basePath: 'pages.thank_you' });

  const imageSrc = gallery[0]?.src || '';

  const { date: checkInDate, time: checkInTime } = getFormattedDateAndTime({
    date: new Date(checkin_at.slice(0, 16)),
    dateVariant: 'full',
  });

  const { date: checkOutDate, time: checkotTime } = getFormattedDateAndTime({
    date: new Date(checkout_at.slice(0, 16)),
    dateVariant: 'full',
  });

  const formattedCheckIn = `${checkInDate} | ${checkInTime}`;
  const formattedCheckOut = `${checkOutDate} | ${checkotTime}`;

  const [accommodates, bedrooms, bathrooms, rating, ratingCount] = getFilterValues({
    formattedFilters: formatted_filters,
    slugs: [ACCOMMODATES_SOURCE, BEDROOMS_SOURCE, BATHROOMS_SOURCE, RATING_SLUG, RATING_COUNT_SLUG],
  });

  const bedroomsCount = bedrooms?.value;
  const bathroomsCount = bathrooms?.value;
  const accommodatesCount = accommodates?.value;

  const bedroomsLabel = bedrooms?.filterAttribute?.title;
  const bathroomsLabel = bathrooms?.filterAttribute?.title;
  const accommodatesLabel = translate.root('custom.guest');

  const mainAmenities = [
    { id: '1', value: bedroomsCount, label: bedroomsLabel, iconName: 'bedrooms' },
    { id: '2', value: bathroomsCount, label: bathroomsLabel, iconName: 'bathrooms' },
    { id: '3', value: accommodatesCount, label: accommodatesLabel, iconName: 'user' },
  ];

  const houseRulesFormattedFilters = formatted_filters.filter(
    (v) => v.filter_attribute?.slug === HOUSE_RULES_SOURCE,
  );
  const { data: filterAttributes } = useGetFilterAttributes({
    purpose: PURPOSE_TYPES['searching'],
  });

  const { filterAttribute: houseRulesFA } = getValueFromFilter({
    filtersArray: filterAttributes || [],
    slug: HOUSE_RULES_SOURCE,
  });

  const { data: houseRulesFAs = [] } = useGetFilterLabels(houseRulesFA?.id || '');

  const houseRules = houseRulesFAs?.map((rule) => {
    const isAllowed = houseRulesFormattedFilters.some((el) => el.slug === rule.slug);
    return {
      ...rule,
      isAllowed,
    };
  });

  return (
    <StyledThankYouTemplateContainer maxWidth="sm">
      <Box bgcolor="background.paper" py={3} px={{ xs: 2, md: 3 }} borderRadius={4}>
        <Typography variant="h5">{translate('title', { country: countryTitle })}</Typography>
        <Typography variant="subtitle1" mb={2}>
          {translate('reservation_code', { code: reference })}
        </Typography>
        <Grid container columnSpacing={2} flexWrap="nowrap">
          <Grid item>
            <Box position="relative" overflow="hidden" width={80} height={80} borderRadius={4}>
              {imageSrc ? (
                <NextImage src={imageSrc} fill style={{ objectFit: 'cover' }} alt="Listing image" />
              ) : (
                <NoImageView
                  typographyProps={{ variant: 'body1' }}
                  iconProps={{ size: 'medium' }}
                />
              )}
            </Box>
          </Grid>

          <Grid item container flexDirection="column">
            <Typography variant="h6" sx={{ wordBreak: 'break-word' }}>
              {title}
            </Typography>
            <AmenitiesStackView items={mainAmenities} typographyProps={{ variant: 'body1' }} />
            {rating?.value ? (
              <RatingView
                rating={Number(rating?.value) || 0} // use default 0 to avoid NaN
                reviewsCount={Number(ratingCount?.value) || 0} // use default 0 to avoid NaN
                typographyProps={{ variant: 'body1' }}
              />
            ) : null}

            <Grid item>
              <NextLink
                href={listingRoute.getPathname(slug)}
                target="_blank"
                className={ThankYouTemplateClasses.link}
              >
                {translate('view_listing')}
              </NextLink>
            </Grid>
          </Grid>
        </Grid>

        <Divider sx={{ my: 2 }} />

        <Grid container flexDirection="column">
          <Typography variant="h6">{translate('guests')}</Typography>
          <Typography variant="subtitle1">
            {pluralize(translate.root('custom.guest_count', { count: users_count }), users_count)}
          </Typography>
        </Grid>

        <Divider sx={{ my: 2 }} />
        <Grid container columnSpacing={3}>
          <Grid item container xs={6} flexDirection="column">
            <Typography variant="h6">{translate('check_in')}</Typography>
            <Typography variant="subtitle1">{formattedCheckIn}</Typography>
          </Grid>
          <Grid item container xs={6} flexDirection="column">
            <Typography variant="h6"> {translate('check_out')}</Typography>
            <Typography variant="subtitle1">{formattedCheckOut}</Typography>
          </Grid>
        </Grid>

        <Divider sx={{ my: 2 }} />

        <Grid container flexDirection="column">
          <Typography variant="h6">{translate('address')}</Typography>
          <Typography variant="subtitle1">{address}</Typography>
        </Grid>

        <Divider sx={{ my: 2 }} />

        <Grid container flexDirection="column">
          <Typography variant="h6">{translate('rules')}</Typography>

          {houseRules.map((item, index) => {
            const { title, isAllowed } = item;
            return (
              <Grid
                item
                sx={{ mt: 1 }}
                key={title + index /* Add 'index' because title may be technically same */}
              >
                <RequirementView allowed={isAllowed} label={title} />
              </Grid>
            );
          })}
        </Grid>
      </Box>
    </StyledThankYouTemplateContainer>
  );
};
